<template lang="html">
  <div class="">
    <h1>cut somke</h1>
    <h1>{{spaceHours}}hours</h1>
    <h1>{{spaceDays}}days</h1>
  </div>
</template>

<script>
export default {
  data(){
    return{
      spaceHours: 0,
      spaceDays: 0
    }
  },
  mounted(){
    this.spaceHours = (((new Date()).getTime() - 1522533600000)/3600000).toFixed(1)
    this.spaceDays = (((new Date()).getTime() - 1522533600000)/(3600000*24)).toFixed(1)
  }
}
</script>

<style lang="css">
  .b1{
    width: 300px;
    height: 250px;
    background: #345;
    padding: 25px 0;
    border-radius: 150px;
  }
  .b2{
    margin: 0 auto;
    width: 250px;
    height: 200px;
    background: #dec;
    padding: 25px 0;
    border-radius: 150px;
  }
  .b3{
    margin: 0 auto;
    width: 200px;
    height: 200px;
    background: #678;
    border-radius: 150px;
  }
</style>



